      }
    }
    await getIZurvive()
  },
  computed: {
    ...mapGetters([
      'getPackageVersion'
    ]),
    buildHash () {
      return process.env.VUE_APP_BUILD_HASH
    },
    gitHash () {
      return process.env.VUE_APP_GIT_HASH
    },
    ftpuname () {
      return this.selectedServer.data ? this.selectedServer.data.gameserver.credentials.ftp.username : null
    }
  },
  methods: {
    onUpdateColorPicked (event) {
      console.log(event)
    },
    onEvolve () {
      this.xmlTreeDirty = true
    },
    onChangeSelectedService (event) {
      const published = this.isServerPublished(event.id)
      const registered = this.isServerRegistered(event.id)
      this.publishServerFeed = published
      this.serverRegistered = registered
    },
    isServerRegistered (sid) {
      const isServerRegistered = this.user.servers ? this.user.servers.filter(server => `${server.sid}` === `${sid}`).length > 0 : false
      return isServerRegistered
    },
    isServerPublished (sid) {
      // console.log('sid', sid)
      const servers = this.user.servers ? this.user.servers : []
      // console.log('servers', servers)
      const isServerPublished = servers
        .filter(server => `${server.sid}` === `${sid}`)
        .reduce((a,c) => c.sactive > 0 ? true : false, false)
      return isServerPublished
    },
    async onRegisterServer (value) {
      if (value === true) {
        this.registerServer.error = null
        this.registerServer.success = false
        this.registerServer.busy = true
        // console.log('using', this.selectedService.id, this.selectedService)
        try {
          const body = {
            name: this.selectedService.details.name,
            nitradoServiceId: this.selectedService.id
          }
          const response = await this.$http.post(`${process.env.VUE_APP_PRISM_API_HOST}/api/v2/servers`, body, {
              withCredentials: true,
              emulateJSON: true
          })
          this.registerServer.success = true
        } catch (e) {
          this.registerServer = false
          this.registerServer.error = e
          console.log(e)
        }
        this.registerServer.busy = false
      }
    },
    async onPublishServerFeed (value) {
      if (value === true) {
        try {
          this.publishServer.error = null
          this.publishServer.success = false
          this.publishServer.busy = true
          const sid = this.selectedService.id
          const schannel = this.killfeedDiscordChannel
          const body = {}
          const { details: { portlist_short } } = this.selectedService
          const response = await this.$http.put(`${process.env.VUE_APP_PRISM_API_HOST}/api/v2/servers/${sid}/${portlist_short}/live/${schannel}`, body, {
            withCredentials: true,
            emulateJSON: true
          })
          this.publishServer.success = true
        } catch (e) {
          this.publishServer.error = e
          console.log(e)
        }
        this.publishServer.busy = false
      } else {
        try {
          this.publishServer.error = null
          this.publishServer.success = false
          this.publishServer.busy = true
          const sid = this.selectedService.id
          const body = {}
          const response = await this.$http.delete(`${process.env.VUE_APP_PRISM_API_HOST}/api/v2/servers/${sid}/live`, body, {
            withCredentials: true,
            emulateJSON: true
          })
          this.publishServer.success = true
        } catch (e) {
          this.publishServer.error = e
          console.log(e)
        }
        this.publishServer.busy = false
      }
    },
    parseServerLogFromString (serverLogString) {

      Object.keys(this.playerPositionsHash)
        .forEach(key => {
          iZurvive._map.removeLayer(this.playerPositionsHash[key].marker)
          iZurvive._map.removeLayer(this.playerPositionsHash[key].label)
        })

      this.playerPositionsHash = {}

      const split = serverLogString.split('\n')

      const transform = split
        .filter(line => line.length > 0)
        .map(line => {
          let match = null
          let lineTypes = [
            'is connected',
            'has been disconnected',
            // 'hit by Player',
            // 'hit by Infected',
            // 'hit by FallDamage',
            // 'killed by Player',
            '##### PlayerList',
            'built',
            'placed',
            'killed by',
            '\>\\)$'
          ]

          match = line.match(/[0-9]*:[0-9]*:[0-9]*/g)
          const time = match ? match[0] : 'Unknown'

          const type = lineTypes.filter(lineType => {
            const regex = new RegExp(`${lineType}`)
            return line.match(regex)
          }).reduce((a,b) => b, ``)
          // console.log(type)

          match = line.match(/Player\s[\"|\'][a0-z9\s\/\-\_]*[\"|\']/g)
          const player = match ? match[0].split('"')[1] : 'ParseWarning'
          match = line.match(/by\sPlayer\s[\"|\'][a0-z9\s\/\-\_]*[\"|\']/g)
          const byPlayer = type === 'hit by Infected' ? 'Infected' : (match ? match[0].split('"')[1] : 'ParseWarning')
          match = null

          match = line.match(/pos=\<[0-9]*.*[0-9]*, [0-9]*.*[0-9]*>/g)
          const coords = match ? match.map(c => {
            const split = c.split(' ')
            return {
              x: parseFloat(split[0].substring(5,split[0].length - 1)).toFixed(1),
              y: parseFloat(split[1].substring(0, split[1].length - 1)).toFixed(1)
            }
          }).reduce((a,b) => b, ``) : { x: null, y: null }
          match = null

          match = line.match(/for\s[0-9]*\sdamage/g)
          const forDamage = match ? parseFloat(match[0].split(' ')[1]) : 0
          match = null

          match = line.match(/from\s[0-9]*\.[0-9]*\smeters/g)
          const fromMeters = match ? parseFloat(match[0].split(' ')[1]) : 0
          match = null

          match = line.match(/placed\s[aA-zZ\s]*/)
          const placed = match ? match[0].substring(`placed `.length, match[0].length) : `Unknown`
          match = null

          match = line.match(/built\s[aA-zZ\s]*/)
          const built = match ? match[0].substring(`built `.length, match[0].length) : `Unknown`
          match = null

          let killedBy = null

          if (line.match(/killed\sby\sLandMineTrap/g)) {
            killedBy = 'LandMineTrap'
          }

          if (line.match(/with\s6-M7\sFrag\sGrenade/g)) {
            killedBy = 'FragGrenade'
          }

          return {
            built,
            placed,
            line,
            fromMeters,
            time,
            player,
            byPlayer,
            killedBy,
            type,
            forDamage,
            coords
          }
        })
        .filter(line => line.time !== 'Unknown')
        .filter(line => line.type.length > 0)
        .filter(line => line.player !== 'Unknown/Dead Entity')
        .filter(line => line.byPlayer !== 'Unknown/Dead Entity')
      
      transform
        .forEach(pc => {
          if (pc.type === '##### PlayerList') {
            Object.keys(this.playerPositionsHash)
              .forEach(key => {
                iZurvive._map.removeLayer(this.playerPositionsHash[key].marker)
                iZurvive._map.removeLayer(this.playerPositionsHash[key].label)
              })
          } else if (pc.coords.x && pc.coords.y) {
            const { x, y } = pc.coords

            let options = null

            let color = 'black', fillColor = 'black'

            switch (pc.type) {
              case 'is connected': fillColor = 'lime'; break;
              case 'has been disconnected': fillColor = 'black'; break;
              // case 'hit by Player': fillColor = 'orange'; break;
              // case 'hit by Infected': fillColor = 'green'; break;
              // case 'hit by FallDamage': fillColor = 'yellow'; break;
              // case 'killed by Player': fillColor = 'red'; break;
              case 'built': fillColor = 'brown'; break;
              case 'placed': fillColor = 'purple'; break;
              case 'killed by': fillColor = 'red'; break;
              case '\>\\)$': fillColor = 'aqua'; break;
            }

            if (fillColor === 'aqua') {

              options = {
                weight: 2.5,
                color: 'aqua',
                fillColor: fillColor,
                fillOpacity: 0.5,
                radius: 10
              }
              
              const latlng = L.LocUtil.locToCoords({
                loc1: x,
                loc2: y
              }, window.archaeonScalingParams)

              // // const html1 = `
              // //   <div style="width:${pc.player.length * 7.33}px;">
              // //     <svg viewBox="0 0 500 500">
              // //       <path style="fill: transparent;" id="curve" d="M73.2,148.6c4-6.1,65.5-96.8,178.6-95.6c111.3,1.2,170.8,90.3,175.1,97" />
              // //       <text width="500">
              // //         <textPath xlink:href="#curve">
              // //           ${pc.player}
              // //         </textPath>
              // //       </text>
              // //     </svg>
              // //   </div>
              // // `

              // const badge = pc.player.split('')
              //   .map((letter, i) => {
              //     return `
              //       <div style="
              //         font: 12px Monaco, MonoSpace;
              //         height: 40px;
              //         position: absolute;
              //         width: 10px;
              //         left: 0;
              //         top: 0;
              //         transform-origin: bottom center;
              //         transform:rotate(${i * 12}deg);
              //       ">${letter}</div>
              //     `
              //   })
              //   .reduce((a,c) => `${a}${c}`, ``)

              // const badgeContainer = `
              //   <div style="
              //     position: relative;
              //     width: 400px;
              //     border-radius: 50%;
              //   ">
              //     ${badge}
              //   </div>
              // `

              const icon = L.divIcon({
                html: `<div style="width:${pc.player.length * 7.33}px;font-family:monospace;">${pc.player}</div>`
              })

              this.playerPositionsHash[pc.player] = {
                marker: L.circleMarker([latlng.lat, latlng.lng], options),
                label: L.marker([latlng.lat, latlng.lng], {
                  icon
                })
              }

              // this.playerStats.markers.push(this.playerPositionsHash[pc.player].marker)
              // this.playerStats.markers.push(this.playerPositionsHash[pc.player].label)

              this.playerPositionsHash[pc.player].marker.on('mouseover', (e) => {
                const content = `${pc.player} @${pc.time} and ${x}/${y}`
                L.popup()
                  .setLatLng(e.latlng) 
                  .setContent(content)
                  .openOn(iZurvive._map)
              })
              this.playerPositionsHash[pc.player].marker.addTo(iZurvive._map)
              this.playerPositionsHash[pc.player].label.addTo(iZurvive._map)
            } else {

              let content = ``

              if (fillColor === 'brown') {
                options = {
                  weight: 2,
                  color: 'yellow',
                  fillColor: 'lime',
                  fillOpacity: 0.5,
                  radius: 3.5
                }
                content = `${pc.player} ${pc.type} ${pc.built} @${pc.time} and ${x}/${y}`
              } else if (fillColor === 'purple') {
                content = `${pc.player} ${pc.type} ${pc.placed} @${pc.time} and ${x}/${y}`
                options = {
                  weight: 2,
                  color: 'yellow',
                  fillColor: 'lightgreen',
                  fillOpacity: 0.5,
                  radius: 3.5
                }
              } else {
                if (pc.killedBy) {
                  content = `${pc.player} ${pc.type} ${pc.killedBy} @${pc.time} and ${x}/${y}`
                } else {
                  content = `${pc.player} ${pc.type} ${pc.byPlayer} from ${pc.fromMeters} meters @${pc.time} and ${x}/${y}`
                }
                options = {
                  weight: 1,
                  color: fillColor,
                  fillColor: fillColor,
                  fillOpacity: 0.75,
                  radius: 2.5
                }
              }

              const latlng = L.LocUtil.locToCoords({
                loc1: x,
                loc2: y
              }, window.archaeonScalingParams)

              const marker = L
                .circleMarker([latlng.lat, latlng.lng], options)

              marker.on('mouseover', (e) => {
                L.popup()
                  .setLatLng(e.latlng) 
                  .setContent(content)
                  .openOn(iZurvive._map)
              })

              marker.addTo(iZurvive._map)

              this.playerStats.markers.push(marker)

            }
          }

        })

      const damageHash = this.damageHash
      const metersHash = this.metersHash
      const killsHash = this.killsHash
      const deathsHash = this.deathsHash
      const statusHash = this.statusHash
      const kdsHash = this.kdsHash

      transform
        .forEach(line => {
          if (line.type === 'is connected') {
            statusHash[line.player] = 'online'
          }
          if (line.type === 'has been disconnected') {
            statusHash[line.player] = 'offline'
          }
        })

      transform
        .forEach(line => {
          if (!damageHash[line.byPlayer]) {
            damageHash[line.byPlayer] = 0
          }
          damageHash[line.byPlayer] += line.forDamage
        })

      transform
        .forEach(line => {
          if (!metersHash[line.byPlayer]) {
            metersHash[line.byPlayer] = line.fromMeters
          }
          metersHash[line.byPlayer] = line.fromMeters > metersHash[line.byPlayer] ? line.fromMeters : metersHash[line.byPlayer]
        })

      transform
        .filter(line => line.byPlayer !== 'Unknown')
        .filter(line => {
          console.log('line.type', line.type, line)
          return line.type === 'killed by'
        })
        // A killed by Player B
        .forEach(line => {
          console.log('line', line, killsHash)
          killsHash[`${line.byPlayer}`] = killsHash[`${line.byPlayer}`] !== undefined ? killsHash[`${line.byPlayer}`] + 1 : 1
          deathsHash[`${line.player}`] = deathsHash[`${line.player}`] !== undefined ? deathsHash[`${line.player}`] + 1 : 1
        })

      transform
        .filter(line => line.byPlayer !== 'Unknown')
        .forEach(line => {
          const { player, byPlayer } = line
          const playerKills = killsHash[player] ? killsHash[player] : 0
          const playerDeaths = deathsHash[player] ? deathsHash[player] : 0
          if ((playerKills < 1) && (playerDeaths < 1)) {
            kdsHash[`${player}`] = 0
          } else {
            kdsHash[`${player}`] = (playerKills/(playerDeaths + 1)).toFixed(2)
          }
        })

      const metersKeys = Object.keys(metersHash)
      const meters = metersKeys.map(key => ({
        player: key,
        meters: metersHash[key],
        status: statusHash[key]
      }))

      const damageKeys = Object.keys(damageHash)
      const damage = damageKeys.map(key => ({
        player: key,
        damage: damageHash[key],
        status: statusHash[key]
      }))

      const killsKeys = Object.keys(killsHash)
      const kills = killsKeys.map(key => ({
        player: key,
        kills: killsHash[key],
        status: statusHash[key]
      }))

      const deathsKeys = Object.keys(deathsHash)
      const deaths = deathsKeys.map(key => ({
        player: key,
        deaths: deathsHash[key],
        status: statusHash[key]
      }))

      const kdsKeys = Object.keys(kdsHash)
      const kds = kdsKeys.map(key => ({
        player: key,
        kds: kdsHash[key],
        status: statusHash[key]
      }))

      meters.sort((a,b) => parseFloat(b.meters) - parseFloat(a.meters))
      this.playerStats.meters = meters.map((stat, i) => ({ ...stat, rank: i + 1 }))
      damage.sort((a,b) => b.damage - a.damage)
      this.playerStats.damage = damage.map((stat, i) => ({ ...stat, rank: i + 1 }))
      kills.sort((a,b) => b.kills - a.kills)
      this.playerStats.kills = kills.map((stat, i) => ({ ...stat, rank: i + 1 }))
      deaths.sort((a,b) => b.deaths - a.deaths)
      this.playerStats.deaths = deaths.map((stat, i) => ({ ...stat, rank: i + 1 }))
      kds.sort((a,b) => b.kds - a.kds)
      this.playerStats.kds = kds.map((stat, i) => ({ ...stat, rank: i + 1 }))

      return Promise.resolve()
    },
    nitradoApiRequest (method, path) {
      const options = {
        'method': method,
        'headers': {
          'Authorization': `Bearer ${this.accountForm.nitradoApiKey}`
        }
      }
      return fetch(`https://api.nitrado.net${path}`, options)
    },
    onTurnOnDiscordBotKillfeed (value) {
      if (value === true) {
        this.discordBot.client = new Client()
        const client = this.discordBot.client
        const prefix = '!'

        client.once('ready', () => {
          // client.guilds.cache.find(guild => console.log(guild))
          // const experimental = client.channels.cache.find(channel => channel.name === 'killbox')
          // experimental.send('i have been summoned')
        })

        client.on('message', async message => {
          console.log(`bot recieved message`, message)
          if (message.content.substring(0,5) === '!rank') {
            if (message.content.substring(6,8) === 'kd') {
              const targetPlayerName = message.content.substring(9,message.content.length)
              const rank = this.playerStats.kds
                .filter(({ player }) => player === targetPlayerName)
                .reduce((a,c,i) => `Player _${c.player}_ ranked ${c.rank}/${this.playerStats.kds.length} with a ${c.kds} k/d ratio.`, ``)
              if (rank.length > 1) {
                message.channel.send(rank)
              } else {
                message.channel.send(`Player _${targetPlayerName}_ not ranked.`)
              }
            } else if (message.content.substring(6,11) === 'kills') {
              const targetPlayerName = message.content.substring(12,message.content.length)
              const rank = this.playerStats.kills
                .filter(({ player }) => player === targetPlayerName)
                .reduce((a,c,i) => `Player _${c.player}_ ranked ${c.rank}/${this.playerStats.kills.length} with ${c.kills} kills.`, ``)
              if (rank.length > 1) {
                message.channel.send(rank)
              } else {
                message.channel.send(`Player _${targetPlayerName}_ not ranked.`)
              }
            } else if (message.content.substring(6,12) === 'deaths') {
              const targetPlayerName = message.content.substring(13,message.content.length)
              const rank = this.playerStats.deaths
                .filter(({ player }) => player === targetPlayerName)
                .reduce((a,c,i) => `Player _${c.player}_ ranked ${c.rank}/${this.playerStats.deaths.length} with ${c.deaths} deaths.`, ``)
              if (rank.length > 1) {
                message.channel.send(rank)
              } else {
                message.channel.send(`Player _${targetPlayerName}_ not ranked.`)
              }
            } else if (message.content.substring(6,12) === 'damage') {
              const targetPlayerName = message.content.substring(13,message.content.length)
              const rank = this.playerStats.damage
                .filter(({ player }) => player === targetPlayerName)
                .reduce((a,c,i) => `Player _${c.player}_ ranked ${c.rank}/${this.playerStats.damage.length} causing ${c.damage} damage.`, ``)
              if (rank.length > 1) {
                message.channel.send(rank)
              } else {
                message.channel.send(`Player _${targetPlayerName}_ not ranked.`)
              }
            } else if (message.content.substring(6,12) === 'meters') {
              const targetPlayerName = message.content.substring(13,message.content.length)
              const rank = this.playerStats.meters
                .filter(({ player }) => player === targetPlayerName)
                .reduce((a,c,i) => `Player _${c.player}_ ranked ${c.rank}/${this.playerStats.kills.length} with longest shot ${c.meters} meters.`, ``)
              if (rank.length > 1) {
                message.channel.send(rank)
              } else {
                message.channel.send(`Player _${targetPlayerName}_ not ranked.`)
              }
            } else {
              message.channel.send(`usage: !rank <kd,kills,deaths,damage,meters> <player-name> \n\te.g., !rank kd sundaysatan\n\tnote: player names are case sensitive`)
            }
          }
          if (message.content.substring(0,12) === '!leaderboard') {
            console.log('leaderboard')
            if (message.content.substring(13,15) === 'kd') {
              const stats = this.playerStats.kds
                .map(({ player, kds, rank }) => `${(parseInt(rank) < 10 ? ` ${rank}` : rank)} | ${(kds < 10) ? ` ${kds}` : kds} | ${player}\n`)
                .filter((stat, i) => i < 25)
                .reduce((a,c) => `${a}${c}`, ``)
              const statsWrapper = `\`\`\` # | k/d   | survivor\n${stats}\`\`\``
              console.log(statsWrapper)
              message.channel.send(statsWrapper)
            } else if (message.content.substring(13,18) === 'kills') {
              const stats = this.playerStats.kills
                .map(({ player, kills, rank }) => `${(parseInt(rank) < 10 ? ` ${rank}` : rank)} |   ${(kills < 10) ? `  ${kills}` : ((kills < 100) ? ` ${kills}` : kills)} | ${player}\n`)
                .filter((stat, i) => i < 25)
                .reduce((a,c) => `${a}${c}`, ``)
              const statsWrapper = `\`\`\` # | kills | survivor\n${stats}\`\`\``
              console.log(statsWrapper)
              message.channel.send(statsWrapper)
            } else {
              message.channel.send(`usage: !leaderboard <kd,kills> \n\te.g., !leaderboard kd`)
            }
          }
          if (message.content.substring(0,5) === '!map') {
            html2canvas(document.querySelector('#map'), { allowTaint: true, foreignObjectRendering: true, useCORS: true })
              .then(canvas => {
                canvas.toBlob(async blob => {
                  const uintArrayBuffer = await blob.arrayBuffer()
                  const attachment = new MessageAttachment(uintArrayBuffer, 'heatmap.png')
                  message.channel.send(attachment)
                })

                // const imageDataUrl = canvas.toDataURL('image/png')
                // console.log('imagedata', imageDataUrl)
                // const encoder = new TextEncoder()
                // const uintArrayBuffer = encoder.encode(imageDataUrl)
                // const attachment = new MessageAttachment(uintArrayBuffer.buffer, 'heatmap.png')
                // message.channel.send(attachment)

                // const imageDataUrl = canvas.toDataURL('image/png')
                // console.log('imagedata', imageDataUrl)
                // const encoder = new TextEncoder()
                // const uintArrayBuffer = encoder.encode(imageDataUrl)
                // const attachment = new MessageEmbed(uintArrayBuffer.buffer, 'heatmap.png')
                // message.channel.send({ embed: attachment })
              })
          }
          // !bounties add 1000 sundaysatan
          if (message.content.substring(0,9) === '!bounties') {
            if (message.content.substring(10,13) === 'add') {
              const reward = message
                .content
                .split(' ')
                .filter(x => parseInt(x))
                .filter((x,i) => i < 1)
                .reduce((a,c) => parseInt(c), -1)
              const playerName = message.content.substring(15 + (`${reward}`.length), message.content.length)
              if ((reward > 0) && (playerName.length > 0)) {
                const wanted = this.playerStats.kills.filter(kill => kill.player === playerName)
                if (wanted.length > 0) {
                  this.bounties.list.push({
                    reward, playerName
                  })
                  message.channel.send(`New bounty worth c${reward} on _${playerName}_.`)
                } else {
                  message.channel.send(`Player _${playerName}_ not found or hasn't killed anyone.`)
                }
              }
            } else if (message.content.substring(10,14) === 'list') {
              const bounties = this.bounties.list
                .map(({ playerName, reward }) => `c${reward} for ${playerName}\n`)
                .reduce((a,c) => `${a}${c}`, ``)
              const bountiesWrapper = `\`\`\` # | credits | survivor\n${bounties}\`\`\``
              console.log(bounties)
              message.channel.send(bounties)
            } else {
              message.channel.send(`usage: !bounties <add,list> <amount> <player>\n\te.g., !bounties add 1000 sundaysatan\n\te.g., !bounties list`)
            }
          }
          // const guildMembers = message.mentions.members.array()
          // guildMembers.filter(({ user }) => user.username === 'rooster')
          //   .forEach(() => {
          //     message.reply('oy')
          //   })
        })

        client.login(this.accountForm.discordApiKey)
        
      } else {
        if (this.discordBot.client) {
          this.discordBot.client.destroy()
        }
      }
    },
    async onTurnOnPlayerStats (value) {
      if (value === true) {
        this.timeSinceLastUpdate = 0
        try {
          this.playerStats.busy = true
          const method = 'GET'
          const sid = this.selectedService.id
          const gameserver = this.selectedServer.data.gameserver
          const { details: { portlist_short } } = this.selectedService
          if (gameserver.game_specific.log_files.filter(file => file.match(/ADM/)).length > 0) {

            let logs = [ ...gameserver.game_specific.log_files.filter(file => file.match(/ADM/)) ]

            logs.reverse()

            let whatthefuck = 1

            await logs.reduce(async (a,c) => await a.then(async () => {

              const logFile = c.replace(`${portlist_short}/`,'')
              const filePath = gameserver.game_specific.path

              this.$buefy.snackbar.open({
                message: `Parsing ${logFile.split('/')[1]} ${whatthefuck}/${logs.length} logs.`,
                type: 'is-warning',
                position: 'is-top-left',
                actionText: 'OK',
                indefinite: false,
                queue: false,
                duration: 15000
              })

              whatthefuck++

              const file = `${filePath}${logFile}`
              const path = `/services/${sid}/gameservers/file_server/download?file=${file}`
              const response = await this.nitradoApiRequest(method, path)
                .then(response => response.json())
              
              if (response && response.data && response.data.token && response.data.token.url) {
                const url = response.data.token.url
                const method1 = 'GET'
                const path1 = url
                const response1 = await fetch(path1, { method: method1 })
                  .then(response => response.body)
                const readableStream = response1
                const ts = new TransformStream(new Uint8ArrayToStringsTransformer())
                const reader = readableStream.pipeThrough(ts).getReader()
                let i = 0, accumulator = ``
                while (true) {
                  const { done, value } = await reader.read()
                  if (value) {
                    accumulator = `${accumulator}${value}\n`
                  }
                  if (done) break
                }
                // const serverLogFilter = accumulator.split('\n').filter(line => line.match(/PURGE/g)).join('\n')
                const serverLogFilter = accumulator
                this.serverLog = `${serverLogFilter}`
                await this.parseServerLogFromString(accumulator)
              }

              return Promise.resolve()

            }), Promise.resolve())

            this.$buefy.snackbar.open({
                message: `Map is synced!`,
                type: 'is-success',
                position: 'is-top-left',
                actionText: 'OK',
                indefinite: false,
                queue: false,
                duration: 15000
            })

            this.playerStats.timer = 150

            this.playerStats.timeout = setTimeout(() => {
              this.playerStats.markers.forEach(m => {
                iZurvive._map.removeLayer(m)
              })
              this.playerStats.markers = []
              this.damageHash = {}
              this.metersHash = {}
              this.killsHash = {}
              this.deathsHash = {}
              this.statusHash = {}
              this.kdsHash = {}
              this.onTurnOnPlayerStats(true)
            }, 120000)

            clearInterval(this.playerStats.timeoutTimer)

            this.playerStats.timeoutTimer = setInterval(() => {
              this.playerStats.timer--
              this.timeSinceLastUpdate++
              if (((this.timeSinceLastUpdate + 1) % 30) === 0) {
                this.$buefy.snackbar.open({
                    message: `Sync is ${this.timeSinceLastUpdate + 1} seconds old...`,
                    type: `is-info`,
                    position: 'is-top-left',
                    actionText: 'OK',
                    queue: false,
                    duration: 30000
                })
              }
            }, 1000)

            this.playerStats.busy = false

          }
        } catch (e) {
          console.log(e)
        }
      } else {
        clearInterval(this.playerStats.timeoutTimer)
        clearTimeout(this.playerStats.timeout)
      }
    },
