    async onSoftDeploy (sid, ftpuname, files) {
      this.softDeploying = true
      await this.stopGameserver(sid)
    },
    async onHardDeploy (sid) {
      this.hardDeploying = true
      this.shouldReinstall = true
      this.stopGameserver(sid)
    },
    connectToWebSocket (sid, wstoken) {
      return new Promise((resolve, reject) => {
        const ws = new WebSocket('wss://websocket.nitrado.net/')

        ws.addEventListener('open', () => {
          this.terminalOutput.push(`* Opened secure real-time connection.`)
          this.terminalOutput.push(`* Attempting to login...`)
          const pojo = {
            action: 'login',
            data: {
              service_id: sid,
              label: 'ni',
              token: wstoken
            }
          }
          const jsonString = JSON.stringify(pojo)
          ws.send(jsonString)
          resolve()
        })

        ws.addEventListener('close', () => {
          console.log({content:`connection closed!`})
          this.terminalOutput.push(`* Connection closed.`)
        })

        ws.addEventListener('message', async (message) => {
          console.log('message', message)
          const wsMessage = JSON.parse(message.data)
          if (this.hardDeploying) {
            const setId = this.selectedGameserverSetting.id
            const sid = this.selectedService.id
            const gameserver = this.selectedServer.data.gameserver
            const ftpuname = gameserver.credentials.ftp.username
            const files = this.files
            if (wsMessage.type === 'service-log') {
              this.terminalOutput.push(`deploy> reinstalling gameserver since ${wsMessage.data.created_at}`)
            } else if (wsMessage.type === 'status') {
              const serverState = wsMessage.data
              this.terminalOutput.push(`deploy> gameserver is ${serverState}`)
              switch (serverState) {
                case 'gs_installation':

                break;
                case 'restarting':

                break;
                case 'stopping':
                  
                break;
                case 'stopped':
                  if (this.didReinstall) {
                    this.terminalOutput.push(`deploy> restoring settings ${setId}`)
                    await this.restoreGameServerSettings(sid, setId)
                    this.terminalOutput.push(`deploy> uploading xml files to ${ftpuname}`)
                    await this.uploadFilesToGameserver(sid, ftpuname, files)
                    await this.startGameserver(sid)
                    this.didReinstall = false
                  }
                  if (this.shouldReinstall) {
                    const method = `POST`
                    const { details: { portlist_short } } = this.selectedService
                    const path = `/services/${sid}/gameservers/games/install?game=${portlist_short}`
                    const response = await this.nitradoApiRequest(method, path)
                    this.shouldReinstall = false
                    this.didReinstall = true
                  }
                break;
                case 'started':
                  if (this.didReinstall) {
                    this.stopGameserver(sid)
                  }
                  if (!this.didReinstall) {
                    this.hardDeploying = false
                    this.terminalOutput.push(`deploy> success at ` + `${new Date()}`.match(/[0-9]*:[0-9]*:[0-9]*/g).join(''))
                  }
                break;
              }
            } else {
              console.log('hmmmm', wsMessage)
            }
          } else if (this.restartingGameserver === true) {
            if (wsMessage.type === 'status') {
              const serverState = wsMessage.data
              this.terminalOutput.push(`restart> gameserver is ${serverState}`)
              if (serverState === 'started') {
                this.terminalOutput.push(`restart> success ` + `${new Date()}`.match(/[0-9]*:[0-9]*:[0-9]*/g).join(''))
                this.restartingGameserver = false
              }
              if (serverState === 'stopped') {
                this.terminalOutput.push(`restart> rebooting in 30 seconds`)
                if (this.restartTimeout === false) {
                  this.restartTimeout = true
                  setTimeout(() => {
                    this.terminalOutput.push(`restart> starting`)
                    this.startGameserver(sid)
                  }, 30000)
                }
              }
            }
          } else if (this.softDeploying === true) {
            if (wsMessage.type === 'status') {
              const serverState = wsMessage.data
              this.terminalOutput.push(`deploy> gameserver is ${serverState}`)
              if (serverState === 'started') {
                this.terminalOutput.push(`deploy> success ` + `${new Date()}`.match(/[0-9]*:[0-9]*:[0-9]*/g).join(''))
                this.softDeploying = false
              }
              if (serverState === 'stopped') {
                this.terminalOutput.push(`deploy> attempting to upload files`)
                await this.uploadFilesToGameserver(sid, this.ftpuname, this.files)
                this.terminalOutput.push(`deploy> starting gameserver`)
                this.startGameserver(sid)
              }
            }
          }
          if (wsMessage.type === 'query') {
            this.terminalOutput.push(`query> ${wsMessage.data.player_current} players`)
              this.$buefy.snackbar.open({
                  message: `There are ${wsMessage.data.player_current} players online.`,
                  type: 'is-white',
                  position: 'is-top-left',
                  actionText: 'OK',
                  indefinite: false,
                  queue: false,
                  duration: 15000
              })
          }
        })

        ws.addEventListener('error', (error) => {
          this.terminalOutput.push(`* Error: ${error}`)
          reject(error)
        })

      })
    },
    async onGetServerByService (selectedService) {
      this.serversBusy = true
      this.terminalOutput = []
      this.selectedService = selectedService
      const schannel = this.user.servers
        .filter(server => `${server.sid}` === `${selectedService.id}`)
        .reduce((a,c) => c.schannel, null)
      this.selectedService.schannel = schannel
      this.killfeedDiscordChannel = schannel
      try {
        const options = {
          method: 'GET',
          headers: {
            'Authorization': `Bearer ${this.accountForm.nitradoApiKey}`
          }
        }
        this.selectedServer = await fetch(`https://api.nitrado.net/services/${selectedService.id}/gameservers`, options)
          .then(response => response.json())
          .then(json => {
            console.log(json)
            return json
          })
        console.log('onGetServerByService', this.selectedServer)
        const gameserver = this.selectedServer.data.gameserver
        this.terminalOutput.push(`* Acquired gameserver info for ${gameserver.query.server_name} @${gameserver.query.version} running ${gameserver.query.map} for ${gameserver.game_human}.`)
        this.terminalOutput.push(`* Server is ${gameserver.status}, with ${gameserver.query.player_current}/${gameserver.query.player_max} players.`)
      } catch (e) {
        console.log(e)
        this.serversError = e
      }
      try {
        this.connectToWebSocket(selectedService.id, selectedService.websocket_token)
      } catch (e) {
        console.log(e)
        this.serversError = e
      }
      try {
        this.gameserverSettings = await this.getGameserverSettings(selectedService.id)
          .then(response => {
            console.log('response here', response)
            return response.json()
          })
          .then(json => {
            console.log(json)
            return json.data.sets
          })
      } catch (e) {
        console.log(e)
        this.serverError = e
      }
      this.serversBusy = false
    },
    async onGetServices () {
      this.servicesBusy = true
      try {
        const options = {
          method: 'GET',
          headers: {
            'Authorization': `Bearer ${this.accountForm.nitradoApiKey}`
