        this.services = await fetch(`https://api.nitrado.net/services`, options)
          .then(response => response.json())
          .then(json => json.data.services)
      } catch (e) {
        this.servicesError = e
      }
      this.servicesBusy = false
    },
    onAccount (account) {
      console.log('on account from app', account)
      this.accountForm = { ...account }
    },
    async onLoadFile (fileContent, paths) {
      console.log('onLoadFile')
      let confirmation = false
      if (this.unsavedChanges || this.xmlTreeDirty) {
        confirmation = await new Promise((resolve, reject) => {
          this.$buefy.dialog.confirm({
            message: 'Commit changes before they are lost?',
            onConfirm: () => resolve(true),
            onCancel: () => resolve(false)
          })
        })
      }
      if (confirmation === true) {
        this.onCommit()
      }
      this.loadedFile = { fileContent, path: paths.join('/') }
      // console.log('loaded file', this.loadedFile)
      const files = this.files
        .filter(file => file.path === this.loadedFile.path)
        .map(file => ({ ...file, id: Math.random() }))
      // console.log('files', files)
      this.selectedFile = files
        .reduce((a, c) => c, null)
      // console.log('selectyed file', this.selectedFile)
      const content = files
        .reduce((a, c) => c.contents, ``)
      this.editor.session.setValue(XmlFormatter(content))
      this.unsavedChanges = false
      this.xmlTreeDirty = false
    },
    onDrawAll (fileContent, paths) {
      console.log(fileContent, paths)
    },
    isFile (x) {
      return typeof x === 'string'
    },
    deepObjectSet (path, value) {
      let schema = this.filesTreeHash
      let pList = path.split('/')
      let len = pList.length
      for (let i = 0; i < len - 1; i++) {
        let elem = pList[i]
        if (!schema[elem]) {
          schema[elem] = {}
        }
        schema = schema[elem]
      }

      schema[pList[len - 1]] = value
    },
    onOpenFile (file, i) {
      return new Promise((resolve, reject) => {
        const { webkitRelativePath } = file
        const reader = new FileReader()
        reader.onload = (event) => {
          const { target: { result } } = event
          const filePath = webkitRelativePath
          this.files.push({
            path: filePath,
            contents: result
          })
          this.deepObjectSet(filePath, result)
          resolve()
        }
        reader.readAsText(file)
      })
    },
    async onOpenFiles (event) {
      const { target: { files } } = event
      this.fileUploadInitialized = false
      await Array.from(files).reduce(async (a, c, i) => {
        await a
        return this.onOpenFile(c, i)
      }, Promise.resolve())
      this.fileUploadInitialized = true
    },
    initResizeObserver () {
      const container = document.getElementsByClassName('sidebar-content')[0]
      const resizeObserver = new ResizeObserver(entries => {
        const height = entries[0].contentRect.height
        document.getElementById('archaeon-xml-editor').style.height = `${height - 365}px`
        document.getElementsByClassName('archaeon-file-menu')[0].style.height = `${height - 50}px`
      })
      resizeObserver.observe(container)
    },
    initOpenFiles () {
      const filesRef = this.$refs['filesInput'].$el.getElementsByTagName('input')[0]
      filesRef.addEventListener('change', this.onOpenFiles)
    },
    onCommit () {
      this.busyCommit = true
      const serializer = new XMLSerializer()
      const xmlTreeContents = this.$refs.dayz_xml_tree.branches.reduce((a, c) => serializer.serializeToString(c.xmlDoc), null)
      const xmlRawContents = this.editor.getValue()
      const newContents = this.xmlTreeDirty ? xmlTreeContents : xmlRawContents
      console.log('this.xmlTreeDirty', this.xmlTreeDirty)
      if (this.xmlTreeDirty === true) {
        this.editor.session.setValue(XmlFormatter(newContents))
      }
      if (this.unsavedChanges) {
        this.$refs.dayz_xml_tree.branches = this.$refs.dayz_xml_tree.branches.map(b => ({ ...b, contents: newContents }))
      }
      this.files = [
        ...this.files.filter(file => file.path !== this.loadedFile.path),
        ...this.files.filter(file => file.path === this.loadedFile.path)
          .reduce((accumulator, currentValue) => {
            return [
              {
                ...currentValue,
                contents: newContents
              }
            ]
          }, [])
      ]
      this.busyCommit = false
      this.unsavedChanges = false
      this.xmlTreeDirty = false
      // this.$forceUpdate()
    },
    initXmlEditor () {
      this.editor = ace.edit('archaeon-xml-editor')
      this.editor.setTheme('ace/theme/pastel_on_dark')
      this.editor.session.setMode('ace/mode/xml')
      this.editor.session.on('change', (delta) => {
        console.log(delta)
        // HERE
        // if ((delta.end.row + 1) === delta.lines.length)
        this.unsavedChanges = true
        document.getElementById('archaeon-xml-editor').style.border = '1px solid orange'
      })
    },
    onRegistered (user) {
      this.loginForm = {
        uname: user.uname
      }
      this.isLoginModalActive = true
    },
    onLoggedin (user) {
      this.onAuthenticated(user)
    },
    onLogin () {
      this.isLoginModalActive = true
    },
    onLoggedout () {
      this.user = { uname: null, uborn: null }
    },
    onAuthenticated (user) {
      this.user = user
      this.accountForm = {
        username: user.uname,
        nitradoApiKey: user.nakey,
        discordApiKey: user.dakey
      }
    },
    getHeader () {
      const version = this.getPackageVersion
      return `Archaeon v${version}`
    },
    async onTraffic () {
      // this.busy = true
      // this.error = false
      // this.success = false
      try {
        const response = await this.$http.post(`${process.env.VUE_APP_PRISM_API_HOST}/api/v2/traffic`)
        // this.success = true
        // this.busy = false
      } catch (error) {
        console.log(error)
        // this.error = true
        // this.busy = false
      }
    }
  }
}
